<template>
  <div>
    <BMainTopComponent :isShowBackBtn="false" />
    <div class="maintenance-strategy-check">
      <!-- 标题和编辑按钮 -->
      <el-row type="flex" align="middle">
        <el-col :span="12">
          <el-row type="flex" justify="start">维保策略</el-row>
        </el-col>
        <el-col :span="12">
          <el-row type="flex" justify="end">
            <el-button type="info" @click="backHandler" plain class="cancel-btn" size="mini"
              >返回</el-button
            >
            <el-button type="primary" @click="editHandler" size="mini">编辑</el-button>
          </el-row>
        </el-col>
      </el-row>
      <!-- 基础信息表单 -->
      <el-row>
        <el-col :span="6">
          <label for="">维保策略：{{ rawData.StrategyName }}</label>
        </el-col>
        <el-col :span="6">
          <label for="">发动机品牌：{{ rawData.EngineBrandName }}</label>
        </el-col>
        <el-col :span="6">
          <label for="">发动机系列：{{ rawData.EngineSeriesName }}</label>
        </el-col>
        <el-col :span="6">
          <label for="">维保策略状态：{{ rawData.IsUsing ? '启用' : '禁用' }}</label>
        </el-col>
        <el-col :span="6">
          <label for="">节拍时长：{{ rawData.BeatLength }}</label>
        </el-col>
        <el-col :span="6">
          <label for="">维保节拍个数：{{ rawData.BeatCount }}</label>
        </el-col>
        <el-col :span="6">&nbsp;</el-col>
        <el-col :span="6" :pull="6">
          <label for="">维保周期时长：{{ rawData.BeatLength * rawData.BeatCount }}</label>
        </el-col>
        <el-col :span="6">
          <label for="">维保周期个数：{{ rawData.CycleCount }}</label>
        </el-col>
        <el-col :span="6">
          <label for=""
            >检修周期时长：{{ rawData.BeatLength * rawData.BeatCount * rawData.CycleCount }}</label
          >
        </el-col>
        <el-col :span="6">
          <label for="">策略主备分类：{{ rawData.Type }}</label>
        </el-col>
      </el-row>
      <!-- 维保事项表格 -->
      <el-row>
        <el-col :span="3">维保事项：</el-col>
        <el-col :span="21">
          <!-- 只要在el-table元素中定义了height属性，即可实现固定表头的表格，而不需要额外的代码。 -->
          <el-table :data="rawData && rawData.StrategyItemList" border height="400">
            <el-table-column type="index" label="序号" width="50"></el-table-column>
            <el-table-column prop="ItemContent" label="维保事项" width="180"> </el-table-column>
            <el-table-column prop="DefaultHourSetting" label="运行时间" width="180">
            </el-table-column>
            <el-table-column prop="DefaultDaySetting" label="绝对时间"> </el-table-column>
            <el-table-column prop="UpDateTimeStr" label="最后编辑时间"> </el-table-column>
            <el-table-column prop="UpDateUserName" label="操作人"> </el-table-column>
          </el-table>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MaintenanceStrategyCheck',
  data() {
    return {
      tableData: [],
      rawData: {}
    }
  },
  methods: {
    editHandler() {
      this.$router.push({
        path: '/setting/agreement-management/maintenance-strategy-new-add',
        query: {
          form: this.rawData
        }
      })
    },
    backHandler() {
      this.$router.push('/setting/agreement-management/maintenance-strategy-new')
    }
  },
  created() {
    console.log(this.$route.query)
    if (this.$route.query && this.$route.query.rawData) {
      const rawData = this.$route.query.rawData
      // 查询维保策略详情
      this.$api.operation
        .GetMaintenanceGensetStrategyDetail({}, { StrategyId: rawData.StrategyId })
        .then((res) => {
          if (res.Code === 1) {
            const data = {
              ...res.Data,
              UpDateUserName: rawData.UpDateUserName,
              StrategyItemList: res.Data.StrategyItemList.map((item) => {
                return {
                  ...item,
                  UpDateTimeStr: item.UpDateTime
                    ? this.$moment(item.UpDateTime).format('yyyy-MM-DD HH:mm:ss')
                    : '',
                  UpDateUserName: rawData.UpDateUserName
                }
              })
            }

            this.$set(this, 'rawData', data)
          }
        })
    }
  }
}
</script>

<style lang="less" scoped>
.maintenance-strategy-check {
  > .el-row:nth-child(1) {
    height: 50px;
    padding: 0 12px;
    background: #f3f3f3;
    border: 1px solid #e4e4e4;
  }
  > .el-row:nth-child(2) {
    height: 130px;
    padding: 20px 0 0 0;
    .el-col {
      height: 33.33%;
    }
    // label {
    //   &::before {
    //     min-width: 10px;
    //     content: '*';
    //     color: red;
    //     font-weight: bold;
    //   }
    // }
  }
  > .el-row:nth-child(3) {
    height: calc(100% - 130px);
  }
}
.theme-dark .maintenance-strategy-check {
  > .el-row:nth-child(1) {
    background: transparent;
  }
}
</style>



